*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  background-color: rgb(184, 179, 179);
  
background-image: url("https://www.transparenttextures.com/patterns/diagonales-decalees.png");
/* This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! */
  padding: 0 16px;
}
header{
  background-color: rgb(223, 190, 5);
  margin: auto -16px;
  padding: 16px 32px;
  box-shadow: 0 0 10px 0 black;
}
header h1{
  color: white;
  font-family: "McLaren",cursive;
  font-weight: 200;

}
footer{
  position: absolute;
  text-align: center;
  bottom: 0;
  width: 100%;
  height: 2.5rem;

}
footer p{
  color: rgb(126, 127, 128);
}
.note{
  background: white;
  border-radius: 7px;
  box-shadow: 0 2px 5px rgb(22, 22, 22);
  padding: 10px;
  width: 240px;
  margin: 16px;
  float: left;

}
.note button{
  color: rgb(223, 190, 5);
  background: none;
  border: none;
  float: right;
}
.note h1{
  font-size: 1.1 em;
  margin-bottom: 6px;
}

form.create-note{
  position: relative;
  width: 480px;
  margin:30px auto 20px auto;
  background: white;
  padding: 15px;
  border-radius: 7px;
  box-shadow: 0 1px 5px grey;
}
form.create-note input,
form.create-note textarea{
  width: 100%;
  border: none;
  padding: 4px;
  outline: none;
  font-size: 1.2em;
  font-family: inherit;
  resize: none;
}
form.create-note button{
  background-color: rgb(223, 190, 5);
  float: right;
  width: 40px;
  height: 40px;
  color: white;
}
@media (max-width:740px) {
  
header{
  background-color: rgb(223, 190, 5);
  margin: auto -16px;
  padding: 10px 12px;
  box-shadow: 0 0 10px 0 black;
}
  .note{
    background: white;
    border-radius: 7px;
    box-shadow: 0 2px 5px rgb(22, 22, 22);
    padding: 5px;
    width: 200px;
    margin: 10px;
    float: left;
  
  }
form.create-note{
  display:inline-block;
  width:240px;
margin: 5px auto;
 /* margin:10px auto 10px auto;*/
  background: white;
  padding: 5px;
  border-radius: 7px;
  box-shadow: 0 1px 5px grey;
}

form.create-note input,
form.create-note textarea{
  width:100%;
  border: none;
  padding: 1px;
  outline: none;
  font-size: 1em;
  font-family: inherit;
  resize: none;
}
.note h1{
  font-size: 1.1em;
  margin-bottom: 3px;
}
form.create-note button{
  background-color: rgb(223, 190, 5);
  float: right;
  width:30px;
  height: 30px;
  color: white;
}

footer{
 /* position: absolute;*/
  text-align: center;
  bottom: 0;
  width: 70%;
  height: 2.5rem;

}
}